                            <div class="col-md-1 col-lg-2 col-xl-1 col-sm-1 col-2">
                                <span class="px-2">
                                    <i class="fa fa-exclamation-circle blink-icon pl-1" aria-hidden="true" v-show="SetupProgress!==100" style="color: red;"></i>
                                    <i class="fa fa fa-cog clr-4 font-30" aria-hidden="true"></i>
                                </span>    
                            </div>
                            <div class="col-md-9 col-lg-8 col-xl-9 col-sm-9 col-9">
                                <div class="px-0" id="Userlist" @click="showadvancesettingpopup('setup')">
                                    <h5 class="card-title font-16 mb-0">{{ $t('SetupDocStorage') }}</h5>
                                    <p class="card-text font-12 pr-5">{{ $t('SetTheModuleSimpleStep') }}</p>
                                </div>
                                   <div class="progress smaller mr-5 my-2" v-if="windowHeight<=568">
                                            <div s class="progress-bar progress-bar-primary progress-bar px-0 text-center memoryused"
                                                id="divConsumendStorage" :class="`${progressColor}`" :style="`width: ${SetupProgress}%; font-weight:500; font-size:14px`"
                                                :title="SetupProgress">{{ SetupProgress }}%</div>
                                        </div>
                            </div>
                            <div class="col-md-2">
                                    <span class="barprogress">
                                <div class="Task mt-2">
                                    <div class="c100 small" v-bind:class="[progressColor, ('p' + SetupProgress)]" v-if="windowHeight>568">
                                        <span>{{SetupProgress}}%</span>
                                        <div class="slice">
                                            <div class="bar"></div>
                                            <div class="fill"></div>
                                        </div>
                                    </div>
                                     
                                </div>
                             
                            </span>
                            <!-- <span class="left-icon-m tickposition d-none">
                                <i class="fa fa-check font-weight-light text-success font-14 mr-2"></i>
                            </span> -->
                            </div>
                             <span class="tickarrowposition d-flex align-items-center">
                                <i class="fa fa-angle-right m-auto text-dark font-20" aria-hidden="true"></i>
                            </span>
                    </a>                 
                </div>
                
                <div class="col-md-12 col-lg-6">
                    <a onmousedown="return false;" href="javascript:void(0)" class="doc-setup short-menu-item bg-white px-0 py-4 my-2 position-relative"
                        data-toggle-tooltip="tooltip" data-original-title="Work Diary"  @click="showadvancesettingpopup('Configuration')">
                       
                        <div class="col-md-1 col-lg-2 col-xl-1 col-sm-1 col-2">
                               <span class="px-2">
                                 <i class="fa fa fa-cog clr-1 font-30" aria-hidden="true"></i>
                               </span> 
                            </div>
                            <div class="col-md-9 col-lg-8 col-xl-9 col-sm-9 col-9" id="Userlist" @click="showadvancesettingpopup('Configuration')">
                               <h5 class="card-title font-16 mb-0">{{$t('Configuration')}}</h5>
                                <p class="card-text font-12 pr-5">{{$t('ConfigureModulePerTheRequirements')}}</p>
                            </div>

                        <!-- <span class="left-icon-m tickposition d-none">
                            <i class="fa fa-check font-weight-light text-success font-14 mr-2"></i>
                        </span> -->
                        <span class="tickarrowposition d-flex align-items-center">
                            <i class="fa fa-angle-right m-auto text-dark font-20" aria-hidden="true"></i>
                        </span>
                    </a>
                </div>        
            </div>
        </div>
    </div>
        <div class="col-12 text-center float-left" v-if="ListView==true">
            <h5 class="my-4">More Settings</h5>
        </div>

        <div class="col-12 col-md-10 m-auto">
          <div class="listing">
                <div class="row">
                    <h6 class="ml-3 mb-0">Doc Storage Setting</h6>
                </div>
                <div class="row card-view bg-border custom-box-width">
                    
                    <div class="col-sm-12 col-lg-3 col-xl-2 col-md-12 div-cardView" data-v-3630b19d="" @click="RedirectToLinks('ManageClientStorage')">
                        <div class="box docattachment py-5 border my-3" data-v-3630b19d="">
                            <i class="nxt-page-icon fa fa-share-square-o" role='button' aria-hidden="true"></i>
                            <div class="box-inner1 d-flex align-items-center m-auto" data-v-3630b19d="">
                                <i class="fa fa-server font-24 m-auto text-white" aria-hidden="true"></i>
                            </div>
                            <div class="row mx-0 align-items-end" data-v-3630b19d="">                                
                                <div class="col-md-12 font-14 mt-2 text-center" data-v-3630b19d="">{{ $t('Manage_Client_Storage') }} </div>
                            </div>
                        </div>
                    </div>
                    <div class="col-sm-12 col-lg-3 col-xl-2 col-md-12 div-cardView" data-v-3630b19d="" @click="RedirectToLinks('ManageUsertStorage')">
                        <div class="box docattachment py-5 border my-3" role='button' data-v-3630b19d="">
                            <i class="nxt-page-icon fa fa-share-square-o" aria-hidden="true"></i>
                            <div class="box-inner2 d-flex align-items-center m-auto" data-v-3630b19d="">
                                <i class="fa fa-users font-20 m-auto text-white" aria-hidden="true"></i>
                            </div>
                            <div class="row mx-0 align-items-end" data-v-3630b19d="">                                
                                <div class="col-md-12 font-14 mt-2 text-center" data-v-3630b19d="">{{ $t('Manage_User_Storage') }}</div>
                            </div>
                        </div>
                    </div>
                     <div class="col-sm-12 col-lg-3 col-xl-2 col-md-12 div-cardView" data-v-3630b19d="" @click="RedirectToLinks('ManageTags')">
                        <div class="box docattachment py-5 my-3 border" role='button' data-v-3630b19d="">
                            <i class="nxt-page-icon fa fa-share-square-o" aria-hidden="true"></i>
                            <div class="box-inner4 d-flex align-items-center m-auto" data-v-3630b19d="">
                                <i class="fa fa-tags font-20 m-auto text-white" aria-hidden="true"></i>
                            </div>
                            <div class="row mx-0 align-items-end" data-v-3630b19d="">                                
                                <div class="col-md-12 font-14 mt-2 text-center" data-v-3630b19d="">{{ $t('ManageTags') }}</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>  
        </div>
       

        <div class="col-12 mt-2" v-if="CardView==true">
            <storagesetup></storagesetup>
        </div>
        <AdvanceSettingModal v-if="showpopup" :IsVisible="showpopup" :tab="CurrentTab" :popupCondition="popupCondition"></AdvanceSettingModal>
    </section>

</template>



<script>
import DataService from "../../services/DataService";
import AdvanceSettingModal from "./Modal/ModalAdvanceSettingsTabs.vue";
import storagesetup from "./../../components/Common/StorageSetup.vue"
export default {
    components: {
        AdvanceSettingModal,
        storagesetup
    },
    data(){
        return {
            showpopup: false,
            CardView:false,
            ListView:true,
            popupCondition:'',
            SetupProgress:0,
             windowHeight: window.innerHeight,
            generalPopupCondition:false,
            CurrentTab:1,
            progressColor:''
        }
    },
    created: function(){
        var vm=this;
        vm.GetStorageSetupStatusDetail();
    },
    mounted() {
    this.$nextTick(() => {
      window.addEventListener('resize', this.onResize);
    })
  },
    updated: function(){
        var vm=this;
        vm.GetStorageSetupStatusDetail();
    },
    methods:{
        OpenDetails() {
            var vm=this;
            vm.$router.push({ name: 'AdvancedSetupDetail' });
        },
        onResize() {
            var vm=this;
            vm.windowHeight = window.innerWidth;
        },
        showgeneralgpopup: function(){
            var vm = this;
            vm.generalPopupCondition=true
            $("#modalGeneralView").modal('show');
                setTimeout(function () {
                    $("#modalManageView").modal('show');
                    setTimeout(function () {

                    }, 200);
                }, 1000);
        },
        showadvancesettingpopup: function(param){
            debugger;
            var vm=this;
            vm.popupCondition=param;
            vm.showpopup=true;
            vm.CurrentTab=1;
            this.ShowManageView = true;
                $("#modalManageView").modal('show');
                setTimeout(function () {
                    $("#modalManageView").modal('show');
                    setTimeout(function () {

                    }, 200);
                }, 1000);
        },
        ChangeView(view){
            var vm=this;
            if(view=="Card"){
                vm.ListView=true
                vm.CardView=false;
            }
            else{
                vm.ListView=false
                vm.CardView=true;
            }
        },
        GetStorageSetupStatusDetail: function () {
            var vm = this;
            DataService.GetStorageSetupStatusDetail().then(function (response) {
                if (response.data != null) {
                    var res = JSON.parse(response.data);
                    vm.SetupProgress = parseInt(res.data[0].TOTAL_PROGRESS);
                    if(vm.windowHeight<568){
                         if(vm.SetupProgress==25)
                        {
                            vm.progressColor="progress-bar progress-bar-striped bg-danger"
                        }
                        else if(vm.SetupProgress==50){
                            vm.progressColor="progress-bar progress-bar-striped bg-warning"
                        }
                        else if(vm.SetupProgress==75){
                            vm.progressColor="progress-bar progress-bar-striped bg-info"
                        }
                        else if(vm.SetupProgress==100){
                            vm.progressColor="progress-bar progress-bar-striped bg-success"
                        }
                    }
                    else{
                    if(vm.SetupProgress==25){
                        vm.progressColor='red'
                    }
                    else if(vm.SetupProgress==50)
                    {
                        vm.progressColor='yellow'
                    }
                    else if(vm.SetupProgress==75)
                    {
                        vm.progressColor='blue'
                    }
                    else if(vm.SetupProgress==100)
                    {
                        vm.progressColor='green'
                    }
                    }
                }
            });
        },
        RedirectToLinks: function(redirectType)
            {
                debugger;
                var envLink=process.env;                
                    if(redirectType=="ManageClientStorage")
                    {
                        window.open(envLink.VUE_APP_ManageClientStorage);
                        return false
                    }
                    else if(redirectType=="ManageUsertStorage")
                    {
                        window.open(envLink.VUE_APP_ManageUserStorage);
                        return false
                    }
                    else if(redirectType=="ManageTags")
                    {
                        window.open(envLink.VUE_APP_ManageTags);
                        return false
                    }
            },
    }
}
</script>
<style>
.c100.green .bar, .c100.green .fill {
    border-color: #8BC34A !important;
}

.clr-1 {
    color: #da9c49;
}

.box-inner1 {
    width: 50px;
    height: 45px;
    background: #da9c49;
}
</style>
